<template>
    <div>
        <!-- Card -->
        <el-card>
            <el-row :gutter="20">
                <!-- Input -->
                <el-col :span="8">  
                    <el-input placeholder="" v-model="queryInfo.query" @clear="getOrderList" clearable>
                        <el-button slot="append" icon="el-icon-search" @click="getOrderList"></el-button>
                    </el-input>
                </el-col>
            </el-row>
            <el-table :data="orderList" stripe>
                <el-table-column type="index" label="#"></el-table-column>
                <el-table-column label="ID" prop="id"></el-table-column>
                <el-table-column label="" prop="number"></el-table-column>
                <el-table-column label="" prop="carrier_user_id"></el-table-column>
                <el-table-column label="" prop="carrier_car_id"></el-table-column>
                <el-table-column label="ID" prop="goods_id"></el-table-column>
                <el-table-column label="" prop="from_city"></el-table-column>
                <el-table-column label="" prop="to_city"></el-table-column>
                <el-table-column label="" >
                    <template slot-scope="scope">
                        <el-tag v-if="scope.row.payment_status == 1" type="success"></el-tag>
                        <el-tag v-if="scope.row.payment_status == 0" type="danger"></el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-tag v-if="scope.row.courier_status == 1" type="success"></el-tag>
                        <el-tag v-if="scope.row.courier_status == 0" type="danger"></el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="" prop="created_at"></el-table-column>
                <el-table-column label="">
                        <template slot-scope="scope">
                                <el-button type="primary" icon="el-icon-edit" size="mini" @click="recoverOrder(scope.row.id)"></el-button>
                        </template>
                </el-table-column>
            </el-table>  
            <!-- Pagination -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[10, 25, 50]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="orderListSize">
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
export default {
    data() {
        return {
            queryInfo: {query: '', pagesize: 10, pagenum: 1, status: 'deleted'},
            orderList: [],
            orderListSize: 0,
            editOrderDialogVisible: false,
            editOrderFormRules: {
                address1: [{required: true,  message: '', trigger: 'blur' },],
                address2: [{required: true,  message: '', trigger: 'blur' },]
            },
            editOrderForm: {
                address1: '',
                address2: '',
            },
            currentOrder: {},
            viewLogisticsDialogVisible: false,
            LogisticsInfo: [],

        }
    },
    created() {this.getOrderList()},
    methods: {
        handleSizeChange(newSize) {
            this.queryInfo.pagesize = newSize
            this.getOrderList()
        },
        handleCurrentChange(newPage) {
            this.queryInfo.pagenum = newPage
            this.getOrderList()
        },
        async getOrderList() {
            const result = await this.$http.get('orders',{params: this.queryInfo})
            console.log(result)
            if(result.data.code !== 200) return this.$message.error(result.data.msg)
            this.$message.success(result.data.msg)
            this.orderList = result.data.data.orders
            this.orderListSize = result.data.data.total
        },
        async recoverOrder(orderID) {
                const confirmResult =  await this.$confirm(', ?', '', { confirmButtonText: '', cancelButtonText: '', type: 'warning'}).catch(err => err)
                if (confirmResult !== 'confirm') return this.$message.info('')
                const result = await this.$http.patch('/orders/' + orderID + "/recover")
                if(result.data.code !== 201) return this.$message.error(result.data.msg)
                this.$message.success(result.data.msg)  
                this.getOrderList()     
        }
    }
}
</script>

<style scoped>

</style>


